 1 0 100%;\n                        @include breakpoints.breakpoint(phablet) {\n                            flex: 1 0 50%;\n                        }\n                        @include breakpoints.breakpoint(laptop) {\n                            flex: 1;\n                        }\n                        > a {\n                            border-bottom: 1px solid #b6afa8;\n                            font-weight: 700;\n                            padding-bottom: 0.75em;\n                            margin-bottom: 0.75em;\n                            display: block;\n                        }\n                        > ul {\n                            padding: 0;\n                            li {\n                                margin-bottom: 12px;\n                            }\n                        }\n                    }\n                    &.hierarchy-level-1 {\n                        > li {\n                            > a {\n                                font-weight: normal;\n                            }\n                        }\n                    }\n                }\n            }\n        }\n        .menu-actions-row {\n            @include transition.transition(max-height 0.3s ease-in-out);\n            text-align: right;\n            background-color: white;\n            max-height: 0;\n            overflow: hidden;\n            &.open {\n                max-height: 56px;\n            }\n            a,\n            button {\n                display: inline-block;\n                padding: 0.25em 1em 0.25em 1.1em;\n                margin: 15px 0;\n                text-decoration: none;\n                color: #414042;\n                cursor: pointer;\n                border: none;\n                font-size: 16px;\n                font-weight: normal;\n                font-family: typography.$default-font;\n                background: none;\n                &:hover {\n                    text-decoration: underline;\n                }\n     